import React from 'react';

const SearchBox = ({ searchChange }) => {
	return (
		<div className="tc-l mt2 pa3">
			<input
				id="number"
				className="input-reset ba b--black-20 pa2 mb2 dib w-30 br1"
				type="number"
				placeholder="88"
				min="0"
				onChange={searchChange}
			/>
			{/* <a className="f6 no-underline grow dib white ba b--white ph3 pv2 mb3" href="">
				Secondary call to action
			</a> */}
		</div>
	);
};
