// fetch(`http://numbersapi.com/88/trivia`)
import React from 'react';
import Header from './components/Header/Header';
import SearchBox from './components/SearchBox/SearchBox';
import Card from './components/Card/Card';
import { useState, useEffect } from 'react';

export const App = () => {
	const [ searchfield, setSearchfield ] = useState(`88`);
	const [ metaData, setMetaData ] = useState('');
	useEffect(
		() => {
			fetch(`http://numbersapi.com/${searchfield}/trivia?json`)
				.then((resp) => resp.json())
				.then((data) => setMetaData(data));
			document.title = `${searchfield}`;
		},
		[ searchfield ],
	);
	// console.log(metaData);
	const onSearchChange = (event) => {
		setSearchfield(event.target.value);
		// console.log(event.target.value);
	};

	return (
		<div>
			<Header />
			<SearchBox searchChange={onSearchChange} />
			<Card numData={metaData} />
		</div>
	);
};

export default App;

/**
 * URL STRUCTURE
 * Just hit http://numbersapi.com/number/type to get a plain text response, where
 *
	* type is one of trivia, math, date, or year. Defaults to trivia if omitted.
	* number is
		* an integer, or
		* the keyword random, for which we will try to return a random available fact, or
		* a day of year in the form month/day (eg. 2/29, 1/09, 04/1), if type is date
		* ranges of numbers
 */
