import React from 'react';

const NumberCard = ({ numData }) => {
	const { number, text } = numData;
	return (
		<article className="center mw5 mw6-ns br3 hidden ba b--black-10 mv4">
			<h1 className="f4 bg-near-white br3 br--top black-60 mv0 pv2 ph3"># {number}</h1>
			<div className=" pa3 bt b--black-30">
				<p className="f5 lh-copy measure white">{text}</p>
			</div>
		</article>
	);
};

export default NumberCard;
